
.table-display-container {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 4px;
    max-height: 300px;
    overflow-y: auto;
}

.table-display {
    width: 100%;
    border-collapse: collapse;
}

.table-display th,
.table-display td {
    padding: 10px;
    border-bottom: 1px solid #eee;
    text-align: left;
}

.table-display th {
    background-color: #f5f5f5;
    position: sticky;
    top: 0;
    z-index: 1;
}

.table-display tr:last-child td {
    border-bottom: none;
}

.table-display tbody tr:hover {
    background-color: #f9f9f9;
    cursor: pointer;
}